*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


nav span{
  position: absolute;
  left: 38px;
  top: 14px;
  font-weight: 700;
}
.MuiButton-root {
  padding: 1px;
  min-width: 0px !important;

}

form{
  width: 26%;
  box-shadow: 1px 1px 4px -1px rgba(0, 0, 0, 0.5);
}

input,textarea{
  outline: none;
}
.text-danger {
  color: #dc3545!important;
  font-size: 26px;
  font-weight: bold;
}
.main{
  width: 20%;
  min-height: 116px !important;
  padding: 0 2px 2px 10px;
  margin: 1px 15px 1px 50px;
  border-radius: 12px;
  box-shadow: 1px 1px 4px -1px rgba(0, 0, 0, 0.5);
}
.main p{
  word-break: break-all;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.note{
  border-radius: 12px;
}
.note input{
  font-family: monospace;
    font-size: 20px;
    font-weight: 700;
}

.noteCss{
  display:flex;
   flex-wrap:wrap;
   justify-content:start
}

@media (max-width:400px) {
  form{
    width: 85%;
  }
  .main{
    width: 74%;
    margin: auto;
  }
  .noteCss{
    display:flex;
     flex-wrap:wrap;
     margin: 0;
     justify-content:center;
  }
  
}

@media (min-width:401px) and (max-width:500px){
  form{
    width: 55%;
  }
  .main{
    width: 46%;
    margin: auto;
  }
}

@media (min-width:501px) and (max-width:600px){
  form{
    width: 55%;
  }
  .main{
    width: 44%;
    margin: auto;
  }
}

@media (min-width:601px) and (max-width:700px){
  form{
    width: 45%;
  }
  .main{
    width: 34%;
    margin: auto;
  }
}

@media (min-width:701px) and (max-width:800px){
  form{
    width: 35%;
  }
  .main{
    width: 29%;
    margin: 1px 4px 1px 21px;
  }
}

@media (min-width:801px) and (max-width:900px){
  form{
    width: 30%;
  }
  .main{
    width: 27%;
    margin: 1px 26px 1px 24px;
  }
}

@media (min-width:901px) and (max-width:1000px){
  form{
    width: 30%;
  }
  .main{
    width: 26%;
  }
}
@media (min-width:1001px) and (max-width:1200px){
  form{
    width: 30%;
  }
  .main{
    width: 22%;
    margin: 1px 9px 1px 21px;
  }
}
@media (min-width:1201px) and (max-width:1300px){
  form{
    width: 30%;
  }
  .main{
    width: 21%;
    margin: 1px 24px 1px 24px;
  }
}